<template>
  <div style="width : 50%;margin: auto;margin-top: 3rem;">
    <div class="form-group" >
      <label for="userid">ID</label>
      <input 
      v-model="userid"
      type="text"
      class="form-control"
      id="userid"
      placeholder="Enter ID"
      name="userid" />
    </div>

    <div class="form-group" >
      <label for="userpwd">Password</label>
      <input
      v-model="userpwd"
      type="password"
      class="form-control"
      id="userpwd"
      placeholder="Enter password"
      name="userpwd" />
    </div>
    
    <div class="form-group" >
      <label for="username">Name</label>
      <input 
      v-model="username"
      type="text"
      class="form-control"
      id="username"
      placeholder="Enter Name"
      name="username" />
    </div>
    <div class="form-group" >
      <label for="email">E-mail</label>
      <input 
      v-model="email"
      type="text"
      class="form-control"
      id="email"
      placeholder="Enter Email"
      name="email" />
    </div>

    <div class="form-group" >
      <label for="address">Address</label>
      <input 
      v-model="address"
      type="text"
      class="form-control"
      id="address"
      placeholder="Enter Address"
      name="address" />
    </div>
    <!-- <template slot="raw-content"> -->
    &nbsp; &nbsp;
    <n-button type="primary" @click="joinMember" round simple>Join</n-button> 
    &nbsp; &nbsp;
    <n-button type="primary" @click="updateMember" round simple>Update</n-button>
    <!-- </template> -->

    <modal
    :show.sync="modals.classic"
    :show-close="true"
    headerClasses="justify-content-center"
    type="mini"
    id="loginForm">

        <div slot="header" class="modal-profile">
          <i class="now-ui-icons users_circle-08"></i>
        </div>
        <card type="login" plain>
          <input 
          v-model="userid"
          type="text"
          class="form-control"
          id="userid"
          placeholder="Enter User ID"
          name="userid" />
          <br>
          <input
          v-model="userpwd"
          type="password"
          class="form-control"
          id="userpwd"
          placeholder="Enter password"
          name="userpwd" />
          <template slot="raw-content">
            &nbsp; &nbsp;
            <n-button type="primary" @click="goLogin" round simple>Login</n-button> 
            &nbsp; &nbsp;
            <n-button type="primary" @click="movePage" round simple>Join</n-button>
          </template>
        </card>
    </modal>
  </div>
</template>

<script>
import { Card, Button, Modal } from '@/components';
import http from '@/util/http-common';
import store from "@/store/store.js";

  export default {
    name: 'member-form',
    components: {
      Card,
      Modal,
      [Button.name]: Button
    },
    data: function(){
      return{
        userid: '',
        userpwd: '',
        username: '',
        email: '',
        address: '',
        modals: {
          classic: false,
          mini: false
        }
      }
    },
    methods: {
      joinMember(){
        http
          .post('/member', {
            userid: this.userid,
            userpwd: this.userpwd,
            username: this.username,
            email: this.email,
            address: this.address
          })
          .then(({ data }) => {
            if (data === 'success') {
              alert('등록이 완료되었습니다.');
              this.modals.classic = true;
            }
          })
          .catch(() => {
            alert('등록 처리시 에러가 발생했습니다.');
          });
      },
      updateMember(){
        http
          .put(`/member/${this.userid}`, {
            userid: this.userid,
            userpwd: this.userpwd,
            username: this.username,
            email: this.email,
            address: this.address
          })
          .then(({ data }) => {
            if (data === 'success') {
              alert('수정이 완료되었습니다.');
              this.$router.push('/');
            }
          })
          .catch(() => {
            alert('수정 처리시 에러가 발생했습니다.');
          });
      },
      goLogin(){
        store.dispatch('login', { userid: this.userid, userpwd: this.userpwd, url: '/qna' });
        this.modals.classic = false;
      },
      goLogout(){
        store.dispatch('logout', { url: '/' });
      },
      movePage(){
        this.$router.push('/member');
        this.modals.classic = false;
      }
    }
  }
</script>

<style lang="scss">
#javascriptComponents {
  .modal-buttons,
  .popover-buttons,
  .tooltip-buttons {
    .btn + .btn {
      margin-left: 3px;
    }
  }
}
</style>