<template>
  <div style="width : 50%;margin: auto;margin-top: 3rem;">
    <div class="form-group" >
      <label for="favoriteArea">관심지역</label>
      <input 
      v-model="favoriteArea"
      type="text"
      class="form-control"
      id="favoriteArea"
      placeholder="Enter ID"
      name="favoriteArea" />
    </div>
  
    &nbsp; &nbsp;
    <n-button type="primary" @click="deleteFavoriteArea" round simple>Delete</n-button> 
    &nbsp; &nbsp;
    <n-button type="primary" @click="updateFavoriteArea" round simple>Update</n-button>
    

  </div>
</template>

<script>
import { Button } from '@/components';
import http from '@/util/http-common';
import store from "@/store/store.js";

  export default {
    name: 'member-form',
    components: {
      [Button.name]: Button
    },
    data: function(){
      return{
        favoriteArea: '',
      }
    },
    methods: {
      updateFavoriteArea(){
        http
          .post('/favorite', {
            id : store.state.userInfo.userid,
            dong_name: this.favoriteArea,
            dong_code: '11111111',
          })
          .then(({ data }) => {
            if (data === 'success') {
              alert('등록이 완료되었습니다.');
              this.modals.classic = true;
              this.$router.push('/favorite/form');
            }else{
              alert('이미등록되있는 동입니다.');
            }
          })
          .catch(() => {
            if(store.state.userInfo.userid == null){
              alert('로그인 해주세요');
            }
          });
      },
      deleteFavoriteArea(){
        http
          .delete(`/favorite/${store.state.userInfo.userid}/${this.favoriteArea}`)
          .then(({ data }) => {
            let msg = '등록되어있지 않은 동입니다.';
            if (data == 1) {
              msg = '삭제가 완료되었습니다.';
              this.$router.push('/favorite/form');
            }
            alert(msg);
          })
          .catch(() => {
             if(store.state.userInfo.userid == null){
              alert('로그인 해주세요');
            }
          });
      },
    }
  }
</script>

<style lang="scss">
#javascriptComponents {
  .modal-buttons,
  .popover-buttons,
  .tooltip-buttons {
    .btn + .btn {
      margin-left: 3px;
    }
  }
}
</style>