<template>
  <div>
    <table class="table table-bordered w-50" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem;"> 
      <tr>
        <th>번호</th>
        <td>{{ item.qna_no }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.qna_userid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.qna_title }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.qna_datetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ item.qna_content }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <router-link to="/qna/qnaList"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/qna/qnaUpdate?no=' + item.qna_no"
        ><button class="btn btn-primary">수정</button></router-link
      >
      <router-link :to="'/qna/qnaDelete?no=' + item.qna_no"
        ><button class="btn btn-primary">삭제</button></router-link
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
export default {
  name: 'detail',
  computed: {
    ...mapGetters(['item']),
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
