<template>
  <div>
    <table class="table table-bordered w-50" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem;">
      <tr>
        <th>번호</th>
        <td>{{ item.no }}</td>
      </tr>
      <tr>
        <th>가게이름</th>
        <td>{{ item.shopname }}</td>
      </tr>
      <tr>
        <th>대분류</th>
        <td>{{ item.codename1 }}</td>
      </tr>
      <tr>
        <th>중분류</th>
        <td>{{ item.codename2 }}</td>
      </tr>
      <tr>
        <th>소분류</th>
        <td>{{ item.codename3 }}</td>
      </tr>
      <tr>
        <th>상세분류</th>
        <td>{{ item.codename4 }}</td>
      </tr>
      <tr>
        <th>우편번호</th>
        <td>{{ item.postcode }}</td>
      </tr>
      <tr>
        <th>동</th>
        <td>{{ item.dong }}</td>
      </tr>
      <tr>
        <th>주소</th>
        <td>{{ item.address }}</td>
      </tr>
    </table>
    
 <div id="map" style="width:500px;height:400px;margin : auto;"></div>
 
    <br >
    <div class="text-center">
      <router-link to="/store/storeList">
      <button class="btn btn-primary">목록</button>
        </router-link>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
export default {
  name: 'detail',
  computed: {
    ...mapGetters(['item']),
    
  },
  data(){
        return {
            map: ''
        }
    },
    mounted() { window.kakao && window.kakao.maps ? this.initMap() : this.addScript(); }, 
    methods : { 
        initMap() {
          //내 디비 ""가 붙어서 있어서 이렇게 함
                var lengthLat = this.item.lat.length-2;
                var lengthLng = this.item.lng.length-2;
                var latsub = this.item.lat.substring(1,lengthLat);
                var lngsub = this.item.lng.substring(1,lengthLng);
                let container = document.getElementById('map');
                 let getLatLng = new kakao.maps.LatLng(latsub,lngsub);
                let options = {
                    center: getLatLng,
                    level: 3
                };
        //원래꺼
        // let container = document.getElementById('map');
             //   let getLatLng = new kakao.maps.LatLng(this.item.lat, this.item.lng);
           //     let options = {
          //          center: getLatLng,
         //           level: 3
        //        };

                this.map = new kakao.maps.Map(container, options);

                // 마커를 생성합니다
              let marker = new kakao.maps.Marker({
                position: getLatLng
              });

              // 마커가 지도 위에 표시되도록 설정합니다
              marker.setMap(this.map);
            },
        addScript() {
                const script = document.createElement('script'); /* global kakao */
                script.onload = () => kakao.maps.load(this.initMap);
                script.src =
                    'http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=1a25b17b57693ae5c28f18742819e5f0';
                document.head.appendChild(script);
            },
    }
};
</script>
