<template>
  <div>
    <div class="page-header page-header-small">
      <parallax
        class="page-header-image"
        style="background-image: url('img/bg6.jpg')"
      >
      </parallax>
      <div class="content-center">
        <div class="container">
          <h1 class="title">Store Information</h1>
        </div>
      </div>
    </div>
       <div v-if="items.length">
       <table class="table table-bordered table-condensed" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem; "> 
        <colgroup>
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '30%' }" />
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '20%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
			  	<th>가게이름</th>
			  	<th>대분류</th>
		  		<th>중분류</th>
		  		<th>소분류</th>
          <th>상세분류</th>
        </tr>
        <list-row
          v-for="(item, index) in items"
          :key="`${index}_items`"
          :no="item.no"
          :shopname ="item.shopname"
          :codename1="item.codename1"
          :codename2="item.codename2"
          :codename3="item.codename3"
          :codename4="item.codename4"
       />
       </table>
      </div>
    <div v-else>선호지역이 없습니다. 선호지역을 등록해주세요</div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import ListRow from '@/components/Store/Row.vue';
import Card from '@/components';
import store from "@/store/store.js";
export default {
  name: 'StoreList',
  components: {
    ListRow,
    Card
  },
  data: function() {
    return {
      items: [],
      userFavorite: [],
     id : store.state.userInfo.userid,
      dong : '???'
      // imgSrc: "..."
    };
  },
  
  created() {
    http
      .get(`/favorite/${this.id}`)
      .then(({ data }) => {
        this.userFavorite = data;
        if(this.userFavorite.length != 0){
         for(var i =0; i<this.userFavorite.length; i++){
          this.dong = this.userFavorite[i].dong_name;
          this.createDong();
        }
      }
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
    });
    
    
  },
  methods : {
    createDong() {
      http
        .get(`/store/${this.dong}`)
        .then(({ data }) => {
          if(data.length>1){
            for(var i =0 ;i<data.length; i++){
              this.items.push(data[i]);
            }
          }else{
            this.items.push(data);
          }
         })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },
  }
};
</script>
