<template>
  <navbar
    position="fixed"
    type="primary"
    :transparent="transparent"
    :color-on-scroll="colorOnScroll"
    menu-classes="ml-auto"
  >
    <template >
      <router-link v-popover:popover1 class="navbar-brand" to="/">
        HappyHouse
      </router-link>
      <el-popover
        ref="popover1"
        popper-class="popover"
        placement="bottom"
        width="200"
        trigger="hover"
      >
      <div class="popover-body">
          Designed by TwoHyun. Coded by TwoHyun
      </div>
      </el-popover>
    </template>
    <template slot="navbar-menu">
      <drop-down
              tag="li"
              title="Search"
              icon="now-ui-icons design_image"
              class="nav-item"
      >
        <nav-link to="/housedeal">
          <i class="now-ui-icons education_paper"></i> Housedeal
        </nav-link>
        <nav-link to="/favorite">
          <i class="now-ui-icons education_paper"></i> FavoriteArea
        </nav-link>
        <nav-link to="/store">
          <i class="now-ui-icons education_paper"></i> FavoriteStore
        </nav-link>
      </drop-down>
      <drop-down
              tag="li"
              title="Help"
              icon="now-ui-icons design_app"
              class="nav-item"
      >
        <nav-link to="/qna">
          <i class="now-ui-icons design_bullet-list-67"></i> QnA
        </nav-link>
        <nav-link to="/notice">
          <i class="now-ui-icons design_bullet-list-67"></i> Notice
        </nav-link>
      </drop-down>
        <drop-down
        tag="li"
        title="MyPage"
        icon="now-ui-icons users_single-02"
        class="nav-item">
        <nav-link to="/member">
          <i class="now-ui-icons users_single-02"></i> Profile
        </nav-link>
        <nav-link to="/favorite/form">
          <i class="now-ui-icons users_single-02"></i> MyFavorite
        </nav-link>
      </drop-down>

      <!-- Model Login-->
      <li class="nav-item" v-if="$store.state.isLogin">
        <n-button type="primary" @click="goLogout">
          Logout
        </n-button>
      </li>
      <li class="nav-item" v-else>
        <n-button type="primary" @click.native="modals.classic = true">
          Login
        </n-button>
      </li>
      <modal
      :show.sync="modals.classic"
      :show-close="true"
      headerClasses="justify-content-center"
      type="mini"
      id="loginForm"
      >
        <div slot="header" class="modal-profile">
          <i class="now-ui-icons users_circle-08"></i>
        </div>
        <card type="login" plain>
          <input 
          v-model="userid"
          type="text"
          class="form-control"
          id="userid"
          placeholder="Enter User ID"
          name="userid" />
          <br>
          <input
          v-model="userpwd"
          type="password"
          class="form-control"
          id="userpwd"
          placeholder="Enter password"
          name="userpwd" />
          <template slot="raw-content">
            &nbsp; &nbsp;
            <n-button @click="goLogin" type="primary" round simple>Login</n-button> 
            &nbsp; &nbsp;
            <n-button type="primary" @click="movePage" round simple>Join</n-button>
          </template>
        </card>
      </modal>

    </template>
  </navbar>
</template>

<script>
import { DropDown, NavbarToggleButton, Navbar, NavLink,Card, Button, Modal, FormGroupInput } from '@/components';
import MainFooter from '@/layout/MainFooter';
import { Popover, Tooltip, DatePicker } from 'element-ui';
import store from "@/store/store.js";

export default {
  name: 'main-navbar',
  props: {
    transparent: Boolean,
    colorOnScroll: Number
  },
  components: {
    DropDown,
    Navbar,
    NavbarToggleButton,
    NavLink,
    [Popover.name]: Popover,
    Card,
    MainFooter,
    Modal,
    [Button.name]: Button,
    [Popover.name]: Popover,
    [Tooltip.name]: Tooltip,
    [DatePicker.name]: DatePicker,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      userid: "",
      userpwd: "",
      modals: {
        classic: false,
        mini: false
      },
      pickers: {
        datePicker: ''
      }
    };
  },
  methods: {
    goLogin(){
      store.dispatch('login', { userid: this.userid, userpwd: this.userpwd, url: '/' });
      this.modals.classic = false;
    },
    goLogout(){
      store.dispatch('logout', { url: '/' });
    },
    movePage(){
      this.$router.push('/member');
      this.modals.classic = false;
    }
  }
};
</script>

<style lang="scss">
#javascriptComponents {
  .modal-buttons,
  .popover-buttons,
  .tooltip-buttons {
    .btn + .btn {
      margin-left: 3px;
    }
  }
}
</style>

