<template>
  <div>
    <table class="table table-bordered w-50" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem;">
      <tr>
        <th>번호</th>
        <td>{{ item.notice_no }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.notice_userid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.notice_title }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.notice_datetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ item.notice_content }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <router-link to="/notice/noticeList"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/notice/noticeUpdate?no=' + item.notice_no"
        ><button class="btn btn-primary">수정</button></router-link
      >
      <router-link :to="'/notice/noticeDelete?no=' + item.notice_no"
        ><button class="btn btn-primary">삭제</button></router-link
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
export default {
  name: 'detail',
  computed: {
    ...mapGetters(['item']),
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
