<template>
  <div>
    <table class="table table-bordered w-50" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem;">
      <tr>
        <th>번호</th>
        <td>{{ item.no }}</td>
      </tr>
      <tr>
        <th>동</th>
        <td>{{ item.dong }}</td>
      </tr>
      <tr>
        <th>코드</th>
        <td>{{ item.code }}</td>
      </tr>
      <tr>
        <th>거래량</th>
        <td>{{ item.dealAmount }}</td>
      </tr>
      <tr>
        <th>건축년도</th>
        <td>{{ item.buildYear }}</td>
      </tr>
      <tr>
        <th>거래년도</th>
        <td>{{ item.dealYear }} {{item.dealMonth}} {{item.dealDay}}</td>
      </tr>
      <tr>
        <th>넓이</th>
        <td>{{ item.area }}</td>
      </tr>
      <tr>
        <th>층</th>
        <td>{{ item.floor }}</td>
      </tr>
      <tr>
        <th>지번</th>
        <td>{{ item.jibun }}</td>
      </tr>
      <tr>
        <th>거래타입</th>
        <td>{{ item.type }}</td>
      </tr>
      <tr>
        <th>거래금액</th>
        <td>{{ item.area }}</td>
      </tr>
      <tr>
        <th>이름</th>
        <td>{{ item.aptName }}</td>
      </tr>
    </table>
     <div id="map" style="width:500px;height:400px;margin : auto;"></div>
    <br>

    <div class="text-center">
      <router-link to="/housedeal/housedealList"
        ><button class="btn btn-primary">목록</button></router-link
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';

export default {
  name: 'detail',
  computed: {
    ...mapGetters(['item']),
  },
   data(){
        return {
            map: ''
        }
    },
    mounted() { window.kakao && window.kakao.maps ? this.initMap() : this.addScript(); }, 
    methods : { 
        initMap() {
                let container = document.getElementById('map');
                let getLatLng = new kakao.maps.LatLng(this.item.lat, this.item.lng);
                let options = {
                    center: getLatLng,
                    level: 3
                };

                this.map = new kakao.maps.Map(container, options);

                // 마커를 생성합니다
              let marker = new kakao.maps.Marker({
                position: getLatLng
              });

              // 마커가 지도 위에 표시되도록 설정합니다
              marker.setMap(this.map);
            },
        addScript() {
                const script = document.createElement('script'); /* global kakao */
                script.onload = () => kakao.maps.load(this.initMap);
                script.src =
                    'http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=1a25b17b57693ae5c28f18742819e5f0';
                document.head.appendChild(script);
            },
    }
};
</script>
