<template>
  <div>
    <div class="page-header page-header-small">
      <parallax
        class="page-header-image"
        style="background-image: url('img/bg6.jpg')"
      >
      </parallax>
      <div class="content-center">
        <div class="container">
          <h1 class="title">Favorite List</h1>
        </div>
      </div>
    </div>
    
    <div style="text-align: center; margin-top : 30px">
      <div v-if="items.length">
        <li v-for="item in items" v-bind:key="item.dong">{{ item.dong_name }}</li>
      </div>
      <div v-else>
        <div v-if="!$store.state.userInfo.userid">
         <h3> 로그인 하여 주세요.</h3>
        </div>
        <div v-else>
          <h3>등록된 동이 없습니다.</h3> 
        </div>
      </div>
    </div>
    <edit-Favorite-form> </edit-Favorite-form>
  </div>
  
</template>

<script>
import  EditFavoriteForm from "@/components/Favorite/EditFavoriteForm.vue";
import store from "@/store/store.js";
import http from '@/util/http-common';

export default {
  components: {
    EditFavoriteForm
  },
  data: function() {
    return {
      items: [],
    };
  },
  created() {
    http
      .get(`/favorite/${store.state.userInfo.userid}`)
      .then(({ data }) => {
          this.items = data;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });
  },
};
</script>
