<script>
import { Parallax } from '@/components';
import BasicElements from './components/BasicElementsSection';
import Navigation from './components/Navigation';
import TabsSection from './components/Tabs';
import ProgressPagination from './components/ProgressPagination';
import Notifications from './components/Notifications';
import Typography from './components/Typography';
import JavascriptComponents from './components/JavascriptComponents';
import CarouselSection from './components/CarouselSection';
import NucleoIconsSection from './components/NucleoIconsSection';
import SignupForm from './components/SignupForm';
import ExamplesSection from './components/ExamplesSection';
import DownloadSection from './components/DownloadSection';
import store from "@/store/store.js";
import Logout from "@/components/Logout.vue";
import ListRow from '@/components/Notice/Row.vue';
import http from '@/util/http-common';
import { posix } from 'path';
import { DropDown, NavbarToggleButton, Navbar, NavLink,Card, Button, Modal, FormGroupInput } from '@/components';
import { Popover, Tooltip, DatePicker } from 'element-ui';

export default {
  name: 'index',
  bodyClass: 'index-page',
  components: {
    Parallax,
    BasicElements,
    Navigation,
    TabsSection,
    ProgressPagination,
    Notifications,
    Typography,
    JavascriptComponents,
    CarouselSection,
    NucleoIconsSection,
    SignupForm,
    ExamplesSection,
    DownloadSection,
    Logout,
    ListRow,
    [Button.name]: Button,
    Card
  },
  data(){
    return{
      isLogin : false,
      items :[],
      scrollY:0,
      timer:null,
      view: false,
      country:'',
      city:'',
      humidity:'',
      temp :'',
      humidity :'',
      weather :'',
      description :'',
      icon :'',
      speed :'',
      clouds :'',
      imgURL :''
    }
  },

  created() {
     http
        .get('/notice/pagelink',{
            params: { limit: 5, offset: 0}
          })
        .then(({ data }) => {
          this.items = data
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    window.addEventListener('scroll',this.handleScroll)
  },
  mounted() {
    this.isLogin = store.state.isLogin
  },
  beforeDestroy: function(){
    window.removeEventListener('scroll', this.handleScroll)
  },
  computed: {
      hasResult: function() {
        return this.posts.length > 0
      }
  },
  methods:{
    handleScroll:function(){
      if(this.timer === null){
        this.timer = setTimeout(function(){
          this.scrollY = window.scrollY
          clearTimeout(this.timer)
          this.timer = null
          }.bind(this),10)
      }
    },
    searchWeather() {
      const BASE_URL = 'http://api.openweathermap.org/data/2.5/weather?q=Seoul&appid=5dc753fbb35d7e99e7fd80b06a9a18a7'
      http
      .get(`${BASE_URL}`)
      .then((result) => {
        this.country = result.data.sys.country
        this.city = result.data.name
        this.view = true
        this.temp = result.data.main.temp- 273.15
        this.humidity = result.data.main.humidity
        this.weather =result.data.weather[0].main 
        this.description = result.data.weather[0].description
        this.icon = result.data.weather[0].icon
        this.speed = result.data.wind.speed
        this.clouds = result.data.clouds.all
        this.imgURL = "http://openweathermap.org/img/w/" + result.data.weather[0].icon + ".png";
       

        console.log(result)
       
      })
    }
  }
};

</script>
