import Vue from 'vue';
import Router from 'vue-router';
import Index from './pages/Index.vue';
import MainNavbar from './layout/MainNavbar.vue';
import MainFooter from './layout/MainFooter.vue';

//Housedeal
import HousedealList from './pages/Housedeal/housedealList.vue';
import HousedealRead from './pages/Housedeal/housedealRead.vue';
import HousedealPage from './pages/Housedeal/housedealPage.vue';

//Favorite
import FavoriteList from './pages/Favorite/favoriteList.vue';
import FavoriteRead from './pages/Favorite/favoriteRead.vue';
import FavoriteForm from './pages/Favorite/favoriteProfile.vue';

//Store
import StoreList from './pages/Store/storeList.vue';
import StoreRead from './pages/Store/storeRead.vue';

//QnA
import QnAList from './pages/QnA/qnaList.vue';
import QnACreate from './pages/QnA/qnaCreate.vue';
import QnADelete from './pages/QnA/qnaDelete.vue';
import QnARead from './pages/QnA/qnaRead.vue';
import QnAUpdate from './pages/QnA/qnaUpdate.vue';
import QnAPage from './pages/QnA/qnaPage.vue';

//Notice
import NoticeList from './pages/Notice/noticeList.vue';
import NoticeCreate from './pages/Notice/noticeCreate.vue';
import NoticeDelete from './pages/Notice/noticeDelete.vue';
import NoticeRead from './pages/Notice/noticeRead.vue';
import NoticeUpdate from './pages/Notice/noticeUpdate.vue';
import NoticePage from './pages/Notice/noticePage.vue';

// Member
import MemberForm from './pages/Member/UserProfile.vue';

Vue.use(Router);

export default new Router({
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'index',
      components: { default: Index, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    //Housedeal
    {
      path: '/housedeal',
      components: { default: HousedealPage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      },
      children:[
        {
          path: '/housedeal/list',
          component: HousedealList,
        }
      ]
    },
    {
      path: '/housedeal/housedealList',
      name: 'housedealList',
      components: { default: HousedealPage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      },
      children:[
        {
          path: '/housedeal/list',
          component: HousedealList,
        }
      ]
    },
    {
      path: '/housedeal/housedealRead',
      name: 'housedealRead',
      components: { default: HousedealRead, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },

    //Favorite
    {
      path: '/favorite',
      components: { default: FavoriteList, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/favorite/favoriteList',
      name: 'favoriteList',
      components: { default: FavoriteList, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/favorite/favoriteRead',
      name: 'favoriteRead',
      components: { default: FavoriteRead, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/favorite/form',
      name: 'favoriteFrom',
      components: { default: FavoriteForm, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
      //Store
      {
        path: '/store',
        components: { default: StoreList, header: MainNavbar, footer: MainFooter },
        props: {
          header: { colorOnScroll: 400 },
          footer: { backgroundColor: 'black' }
        }
      },
      {
        path: '/store/storeList',
        name: 'storeList',
        components: { default: StoreList, header: MainNavbar, footer: MainFooter },
        props: {
          header: { colorOnScroll: 400 },
          footer: { backgroundColor: 'black' }
        }
      },
      {
        path: '/store/storeRead',
        name: 'storeRead',
        components: { default: StoreRead, header: MainNavbar, footer: MainFooter },
        props: {
          header: { colorOnScroll: 400 },
          footer: { backgroundColor: 'black' }
        }
      },
    //QnA
    {
      path: '/qna',
      components: { default: QnAPage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      },
      children:[
        {
          path: '/qna/list',
          component: QnAList,
        }
      ]
    },
    {
      path: '/qna/qnaList',
      name: 'qnaList',
      components: { default: QnAPage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      },
      children:[
        {
          path: '/qna/list',
          component: QnAList,
        }
      ]
    },
    {
      path: '/qna/qnaRead',
      name: 'qnaRead',
      components: { default: QnARead, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/qna/qnaCreate',
      name: 'qnaCreate',
      components: { default: QnACreate, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/qna/qnaUpdate',
      name: 'qnaUpdate',
      components: { default: QnAUpdate, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/qna/qnaDelete',
      name: 'qnaDelete',
      components: { default: QnADelete, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    //Notice
    {
      path: '/notice',
      components: { default: NoticePage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      },
      children:[
        {
          path: '/notice/list',
          component: NoticeList,
        }
      ]
    },
    {
      path: '/notice/noticeList',
      name: 'noticeList',
      components: { default: NoticePage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      },
      children:[
        {
          path: '/notice/list',
          component: NoticeList,
        }
      ]
    },
    
    {
      path: '/notice/noticeRead',
      name: 'noticeRead',
      components: { default: NoticeRead, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/notice/noticeCreate',
      name: 'noticeCreate',
      components: { default: NoticeCreate, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/notice/noticeUpdate',
      name: 'noticeUpdate',
      components: { default: NoticeUpdate, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/notice/noticeDelete',
      name: 'noticeDelete',
      components: { default: NoticeDelete, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },
    // 회원관리
    {
      path: '/member',
      name: 'member',
      components: { default: MemberForm, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    },

  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
