<template>
  <div>
    <div class="page-header page-header-small">
      <parallax
        class="page-header-image"
        style="background-image: url('img/bg6.jpg')"
      >
      </parallax>
      <div class="content-center">
        <div class="container">
          <h1 class="title">Housedeal Information</h1>
        </div>
      </div>
    </div>
    <div style="margin-left:20%" class="input-group mt-3 mb-3" >
				<select class="search" id="search" v-model="searchType">
					<option value="dong">동</option>
					<option value="apt">아파트</option>
				</select> 
				<input 
        type="text"
        id="searchName" 
        placeholder="검색할 동/아파트를 입력해주세요" 
        style="width : 50%; "
        v-model="keyword">
			<div class="input-group-append">
				<button class="btn btn-primary" @click="checkHandler"  >Go</button>
			</div>
		</div>
    
    <div v-if="items.length">
       <table class="table table-bordered table-condensed" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem; "> 
        <colgroup>
          <col :style="{ width: '5%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '20%' }" />
          <col :style="{ width: '10%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
			  	<th>동</th>
			  	<th>아파트이름</th>
		  		<th>거래금액</th>
		  		<th>거래종류</th>
        </tr>
        <list-row
          v-for="(item, index) in items"
          :key="`${index}_items`"
          :no="item.no"
          :dong ="item.dong"
          :aptName="item.aptName"
          :dealAmount="item.dealAmount"
          :type="item.type"
      />
      </table>
      </div>
     <div v-else style="text-align: center;"> <h3>찾으시는 항목이 없습니다.</h3> </div>
   </div>
</template>

<script>
import http from '@/util/http-common';
import ListRow from '@/components/Housedeal/Row.vue';
import Card from '@/components';
import store from "@/store/store.js";
export default {
  name: 'housedealList',
  components: {
    ListRow,
    Card
  },
  data: function() {
    return {
      items: [],
      pageLimit : 10,
      pageOffet : 0
    };
  },
  created() {
    this.initComponent();
  },
  watch :{
    '$route.query':function(){
      this.initComponent();
    }
  },
  methods: {
    checkHandler() {
      if(this.searchType == 'dong'){
        this.$store.state.type = 1 ;
      }else{
         console.log('실행되고 있음2');
        this.$store.state.type = 2 ;
      }
      this.initComponent();
      
    },
    initComponent(){
      if(this.$store.state.type == 2){
          http
        .get('/housedeal/pagelink/aptname',{
         params: { aptname: this.keyword, limit: this.pageLimit, offset: `${this.$route.query.no - this.pageLimit}`}
        })
        .then(({ data }) => {
        this.items = data;
         })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
        this.$store.state.type = 2 
        this.$store.state.word = this.keyword 
       }else if(this.$store.state.type == 1){
        http
        .get('/housedeal/pagelink/dong',{
        params: { dong: this.keyword, limit: this.pageLimit, offset: `${this.$route.query.no - this.pageLimit}`}
        })
        .then(({ data }) => {
        this.items = data;
         })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
        this.$store.state.type = 1 
        this.$store.state.word = this.keyword 
      }else{
        http
        .get('/housedeal/pagelink',{
            params: { limit: this.pageLimit, offset: `${this.$route.query.no - this.pageLimit}`}
          })
        .then(({ data }) => {
          this.items = data
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
         this.$store.state.type = 0 
     
      }
     
    }
  }
};
</script>
