import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import http from "@/util/http-common";
import router from '../router.js';
import createPersistedState from "vuex-persistedstate";

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    isLogin: false,
    userInfo: {},
    items: [],
    item: {},
    type : 0,
    word : ''
  },
  getters: {
    items(state) {
      return state.items;
    },
    item(state) {
      return state.item;
    },
    type(state){
      return state.type;
    }
  },
  mutations: {
    
    mutateIsLogin(state, isLogin){
      state.isLogin = isLogin

      console.log(7);

    },
    mutateUserInfo(state, userInfo){
      state.userInfo = userInfo
    },

    mutateSetItems(state, items){
      state.items = items
    },
    mutateSetItem(state, item){
      state.item = item
    }

  },
  actions: {
    login(context, {userid, userpwd, url}) {
      http
        .post("/login", {
          userid: userid,
          userpwd: userpwd
        })
        .then(({ data }) => {
          console.log(data);
          context.commit('mutateIsLogin', true)
          context.commit('mutateUserInfo', data)
          router.push(url);

          // 오류 코드 this in promise-then()
          // Uncaught (in promise) TypeError
          //this.$router.push('/About')
        })
        .catch((error) => {
          if (error.response.status == '404') {
            alert("아이디 또는 비밀번호가 올바르지 않습니다.");
          } else {
             alert('로그인 처리시 에러가 발생했습니다.');
          }
          console.log(error.config);

        });
    },
    logout(context, {url}){
      http
        .post("/logout")
        .then(( data ) => {
          console.log(data);
          context.commit('mutateIsLogin', false)
          context.commit('mutateUserInfo', {})
          router.push(url);
        })
        .catch((error) => {
          alert('로그아웃 처리시 에러가 발생했습니다.');
          console.log(error.config);
        });
    },
    getItems(context) {
      http
        .get('/qna')
        .then(({ data }) => {
          context.commit('mutateSetItems', data);
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },
    getItem(context, payload) {
      http.get(payload).then(({ data }) => {
        context.commit('mutateSetItem', data);
      });
    }
    
  }
});
