<template>
  <div>
    <div class="page-header page-header-small">
      <parallax
        class="page-header-image"
        style="background-image: url('img/bg6.jpg')"
      >
      </parallax>
      <div class="content-center">
        <div class="container">
          <h1 class="title">Notice</h1>
        </div>
      </div>
    </div>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed" style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem;"> 
        <colgroup>
          <col :style="{ width: '5%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '25%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <list-row
          v-for="(item, index) in items"
          :key="`${index}_items`"
          :no="item.notice_no"
          :title="item.notice_title"
          :writer="item.notice_userid"
          :regtime="item.notice_datetime"
        />
      </table>
    </div>
    <div v-else>글이 없습니다.</div>
    <div class="text-center">
      <button class="btn btn-primary" @click="movePage">등록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import ListRow from '@/components/Notice/Row.vue';
export default {
  name: 'noticeList',
  components: {
    ListRow,
  },
  data: function() {
    return {
      items: [],
      pageLimit : 10,
      pageOffet : 0
    };
  },
  created() {
    this.initComponent();
  },
  watch: {
    '$route.query': function(){
      this.initComponent();
    }
  },
  methods: {
     initComponent(){
      http
        .get('/notice/pagelink',{
            params: { limit: this.pageLimit, offset: `${this.$route.query.no - this.pageLimit}`}
          })
        .then(({ data }) => {
          this.items = data
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },
    movePage() {
      this.$router.push('/notice/noticeCreate');
    },
  },
};
</script>

